html {
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
  background-color: #E9E9E9;
  color: #000000;
  font-family: "Open Sans", sans-serif;
  line-height: 1.3;
  height: 100%;
}

main {
  height: 100%;
  background-color: #E9E9E9;
}

.header {
  background-color: #F9F9F9;
  background-color: #f9f9f9;
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 15px 0;
}
.header .image-wrapper {
  margin: 0;
  width: 50%;
  display: flex;
  flex: 0 0 auto;
  justify-content: center;
  position: relative;
  padding: 0 34px;
}
.header .image-wrapper::after {
  content: "";
  border-left: 1px solid #E9E9E9;
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
}
.header .image-wrapper:last-child::after {
  border-left: none;
}
.header .image-wrapper .img-fluid {
  height: 100%;
  object-fit: contain;
  width: 100%;
}
.header .image-wrapper .img-fluid.img--left {
  max-width: 156px;
}
@media only screen and (max-width: 360px) {
  .header .image-wrapper .img-fluid.img--left {
    max-width: 120px;
  }
}
.header .image-wrapper .img-fluid.img--right {
  max-width: 100px;
}
@media only screen and (max-width: 360px) {
  .header .image-wrapper .img-fluid.img--right {
    max-width: 70px;
  }
}

.icon {
  display: flex;
  flex: 0 0 auto;
}
.icon--box {
  width: 24px;
  height: 24px;
  margin-right: 12px;
  background-color: #699FF3;
}
.icon--circle {
  height: 16px;
  width: 16px;
  border-radius: 50%;
}
.icon--circle.green {
  border: 5px solid #4D9D2E;
}
.icon--circle.red {
  border: 5px solid #DC3545;
}
.icon--location {
  width: 20px;
  height: 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/icon-target.png);
}
.icon--search {
  width: 24px;
  height: 24px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/icon-search.png);
}
.icon--station {
  width: 24px;
  height: 28px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/vaccination-stations-icon.png);
}
.icon--interest {
  width: 24px;
  height: 28px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/location-interest-icon.png);
}
.icon--testing {
  width: 24px;
  height: 28px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/testing-sites-icon.png);
}
.icon--buses {
  width: 24px;
  height: 28px;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../dist/img/vaccination-buses-icon.png);
}
.icon.fas {
  color: rgba(40, 39, 39, 0.8);
  font-size: 14px;
}

.input-wrapper {
  position: relative;
}

.custom-checkbox-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  margin: 0 0 20px;
}
.custom-checkbox-wrapper:last-child {
  margin: 0;
}
.custom-checkbox-wrapper.reverse {
  flex-direction: row-reverse;
  justify-content: flex-end;
}
.custom-checkbox-wrapper.reverse .custom-checkbox {
  margin-right: 10px;
}
.custom-checkbox-wrapper .title-wrapper {
  display: flex;
  align-items: center;
}
.custom-checkbox-wrapper .title-wrapper .icon {
  margin-right: 12px;
}
.custom-checkbox-wrapper .form-control--checkbox {
  display: none;
}
.custom-checkbox-wrapper .form-control--checkbox:checked ~ .custom-checkbox .icon--check {
  display: block;
}
.custom-checkbox-wrapper .custom-checkbox {
  width: 32px;
  height: 32px;
  background: #ffffff;
  border: 2px solid #282727;
  box-sizing: border-box;
  border-radius: 4px;
  position: relative;
  display: flex;
  flex: 0 0 auto;
  align-items: center;
  justify-content: center;
}
.custom-checkbox-wrapper .custom-checkbox .icon--check {
  display: none;
  color: #282727;
  font-size: 16px;
}

.badge {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #E9E9E9;
  box-sizing: border-box;
  border-radius: 100px;
  padding: 10px;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  color: #000000;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.list__item {
  display: flex;
  align-items: center;
}
.list__item .icon {
  margin-right: 10px;
}

.custom-radio-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  margin: 0 0 20px;
}
.custom-radio-wrapper.reverse {
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin: 0;
}
.custom-radio-wrapper:last-child {
  margin: 0;
}
.custom-radio-wrapper .title-wrapper {
  display: flex;
  align-items: center;
}
.custom-radio-wrapper .title-wrapper .icon {
  margin-right: 12px;
}
.custom-radio-wrapper .form-control--radio {
  display: none;
}
.custom-radio-wrapper .form-control--radio:checked ~ .custom-radio::after {
  background-color: #37474F;
}
.custom-radio-wrapper .custom-radio {
  width: 32px;
  height: 32px;
  background: #FFFFFF;
  border: 2px solid #282727;
  box-sizing: border-box;
  border-radius: 50%;
  position: relative;
}
.custom-radio-wrapper .custom-radio::after {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  border-radius: 50%;
}

.btn {
  padding: 12px;
  font-family: "Open Sans", sans-serif;
}
.btn--green {
  background: #AECA36;
  border-radius: 4px;
  color: #000000;
}
.btn--white {
  background: #ffffff;
  border-radius: 4px;
  color: #000000;
}
.btn--rounded {
  border-radius: 100px;
}
.btn--sm {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal .modal-dialog .modal-body {
  padding: 15px;
}
.modal .modal-dialog .modal-field {
  margin-bottom: 15px;
}
.modal .modal-dialog .modal-field--list .title-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.modal .modal-dialog .modal-field--list .title-wrapper:last-child {
  margin-bottom: 0;
}
.modal .modal-dialog .modal-field--list .title-wrapper .icon {
  margin-right: 15px;
}
.modal .modal-dialog .modal-field--badge {
  display: flex;
  flex-direction: row;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-field--badge {
    flex-direction: column;
  }
}
.modal .modal-dialog .modal-field--badge .badge {
  margin-right: 10px;
  width: max-content;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-field--badge .badge {
    margin-bottom: 10px;
  }
}
.modal .modal-dialog .modal-field--badge .badge .icon {
  color: #AECA36;
  margin-right: 10px;
}
.modal .modal-dialog .modal-field--badge .badge:last-child {
  margin-right: 0;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-field--badge .badge:last-child {
    margin-bottom: 0;
  }
}
.modal .modal-dialog .modal-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.modal .modal-dialog .modal-title--titleless {
  justify-content: flex-end;
}
.modal .modal-dialog .modal-title .title-wrapper--has-icon {
  display: flex;
  align-items: center;
}
.modal .modal-dialog .modal-title .title-wrapper--has-icon .icon {
  margin-right: 10px;
}
.modal .modal-dialog .modal-title .title-wrapper .subtitle {
  margin: 0;
}
.modal .modal-dialog .modal-title .icon--close {
  cursor: pointer;
  font-size: 27px;
  margin-right: 10px;
}
.modal .modal-dialog .modal-form {
  margin-top: 32px;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-form {
    margin-top: 20px;
  }
}
.modal .modal-dialog .modal-form--filter .form-group {
  padding-bottom: 22px;
  margin-bottom: 22px;
  position: relative;
}
.modal .modal-dialog .modal-form--filter .form-group::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  border-bottom: 1px solid #E9E9E9;
}
.modal .modal-dialog .modal-form--filter .form-group.borderless {
  margin-bottom: 0;
}
.modal .modal-dialog .modal-form--filter .form-group.borderless::after {
  border-bottom: none;
}
.modal .modal-dialog .modal-form--filter .form-group.header {
  justify-content: flex-start;
}
.modal .modal-dialog .modal-form--filter .form-group.header .title-wrapper {
  display: flex;
  align-items: center;
}
.modal .modal-dialog .modal-form--filter .form-group.header .title-wrapper .text.bold {
  font-size: 12px;
  line-height: 125%;
}
.modal .modal-dialog .modal-form .form-group {
  margin-bottom: 30px;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-form .form-group {
    margin-bottom: 20px;
  }
}
.modal .modal-dialog .modal-form .form-group--inline {
  display: flex;
}
.modal .modal-dialog .modal-form .form-group--inline .custom-radio-wrapper {
  margin-right: 30px;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-form .form-group--inline .custom-radio-wrapper {
    margin-right: 15px;
  }
}
.modal .modal-dialog .modal-form .form-group--inline .custom-radio-wrapper:last-child {
  margin-right: 0;
}
.modal .modal-dialog .modal-form .form-group--inline .custom-radio-wrapper .custom-radio {
  margin-right: 10px;
}
.modal .modal-dialog .modal-form .form-group--checkbox {
  display: block;
}
@media only screen and (max-width: 360px) {
  .modal .modal-dialog .modal-form .form-group--checkbox {
    display: none;
  }
}
.modal .modal-dialog .modal-form .form-group--location {
  display: none;
}
.modal .modal-dialog .modal-form .form-group--location.active {
  display: block;
}
.modal .modal-dialog .modal-form .form-group--phone {
  display: none;
}
.modal .modal-dialog .modal-form .form-group--phone.active {
  display: block;
}
.modal .modal-dialog .modal-form .form-group--email {
  display: none;
}
.modal .modal-dialog .modal-form .form-group--email.active {
  display: block;
}
.modal .modal-dialog .modal-form .form-group .form-control,
.modal .modal-dialog .modal-form .form-group .form-select {
  border: 2px solid rgba(40, 39, 39, 0.8);
  box-sizing: border-box;
  border-radius: 8px;
  padding: 14px 19px;
}
.modal .modal-dialog .modal-form .form-group .form-control.disabled,
.modal .modal-dialog .modal-form .form-group .form-select.disabled {
  background: #f8f7f6;
  border: 2px solid #ced4da;
  box-sizing: border-box;
  pointer-events: none;
  color: #ced4da;
}
.modal .title {
  font-weight: bold;
  font-size: 20px;
  line-height: 27px;
  color: #000000;
  margin-bottom: 5px;
}
@media only screen and (max-width: 360px) {
  .modal .title {
    font-size: 18px;
    line-height: 25px;
  }
}
.modal .subtitle {
  font-weight: 600;
  font-size: 18px;
  line-height: 125%;
  color: #000000;
  margin: 0 0 15px;
}
.modal .text {
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
  margin: 0;
}
.modal .bold {
  font-weight: bold;
}
.modal .btn-action {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal .btn-action .btn {
  width: 100%;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
}

.section-index {
  height: 100%;
}
.section-index .iframe-wrapper {
  height: 100%;
}
.section-index .iframe-wrapper .main-iframe {
  height: 100%;
  width: 100%;
}

.section-iframe {
  background-color: #E9E9E9;
  height: calc(100% - 92px);
  padding: 30px;
}
@media only screen and (max-width: 360px) {
  .section-iframe {
    padding: 15px;
  }
}
.section-iframe .content {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  position: relative;
}
.section-iframe .content .iframe-wrapper {
  width: 100%;
  height: auto;
  flex-grow: 1;
  position: relative;
}
.section-iframe .content .iframe-wrapper .map {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 0;
}
.section-iframe .content .iframe-wrapper .button-wrapper {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-iframe .content .iframe-wrapper .button-wrapper .btn--white {
  width: max-content;
  margin: 0 auto;
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.section-iframe .content .iframe-wrapper .button-wrapper .btn--white .icon {
  margin-right: 10px;
}
.section-iframe .form {
  padding: 15px;
  background-color: #282727;
}
.section-iframe .form .form-group {
  margin-bottom: 15px;
}
.section-iframe .form .form-group:last-child {
  margin-bottom: 0;
}
.section-iframe .form .form-group .btn--white {
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  border-radius: 8px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-iframe .form .form-group .btn--white .icon {
  margin-right: 10px;
}
.section-iframe .form .input-wrapper--has-icon .form-control {
  height: 54px;
  font-style: italic;
  font-weight: 500;
  font-size: 16px;
  line-height: 125%;
  color: #000000;
  background: #ffffff;
  border: 2px solid #ffffff;
  box-sizing: border-box;
  border-radius: 8px;
  padding-right: 94px;
}
.section-iframe .form .input-wrapper--has-icon .form-control::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #BEBEBE;
}
.section-iframe .form .input-wrapper--has-icon .form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #BEBEBE;
}
.section-iframe .form .input-wrapper--has-icon .form-control:-ms-input-placeholder {
  /* IE 10+ */
  color: #BEBEBE;
}
.section-iframe .form .input-wrapper--has-icon .form-control:-moz-placeholder {
  /* Firefox 18- */
  color: #BEBEBE;
}
.section-iframe .form .input-wrapper--has-icon .action {
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  bottom: 0;
  right: 4px;
  display: flex;
  align-items: center;
}
.section-iframe .form .input-wrapper--has-icon .action .icon.icon--location {
  margin-right: 14px;
  cursor: pointer;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0px;
}

h1 {
  font-size: 2rem;
}
@media only screen and (min-width: 1000px) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.5rem;
}
@media only screen and (min-width: 1000px) {
  h2 {
    font-size: 2rem;
  }
}

h3 {
  font-size: 1.25rem;
}
@media only screen and (min-width: 1000px) {
  h3 {
    font-size: 1.5rem;
  }
}